import Vue from 'vue'
import App from './App.vue'

Vue.config.productionTip = false

new Vue({
  render: h => h(App),
}).$mount('#app')

Vue.config.errorHandler = function (error, vm, info) {
  // handle error
  // `info` 是 Vue 特定的错误信息，比如错误所在的生命周期钩子
  // 只在 2.2.0+ 可用
  console.log('err:', error, vm, info)
  window.postMessage({
    type: 'vue2errorhandler',
    data: { error }
  }, '*');
}
