<template>
  <div class="home-view">
    <p class="p">外层组件</p>
    <HelloWorld msg="Hello World!" />
    <SigleItem />
    <div>
      <button class="button" @click="handleClick">这是外部组件点击Crash</button>
      <span class="counter">{{ counter > 0 ? counter.data.n : counter }}</span>
    </div>
  </div>
</template>

<script>
import HelloWorld from './components/HelloWorld.vue'
import SigleItem from './components/SigleItem.vue'

export default {
  name: 'App',
  components: {
    HelloWorld,
    SigleItem,
  },
  data: function () {
    return { counter: 0 }
  },
  methods: {
    handleClick() {
      this.counter++;
    },
  },
}
</script>

