<template>
  <div class="sigle-item">
    <h3>
      这是一个单独的组件
    </h3>
    <div>
      <button class="button" @click="handleClick">点击Crash</button>
      <span class="counter">{{ params.counter > 0 ? params.data.counter : params.counter }}</span>
      <!-- <span class="counter">{{ params.data.counter }}</span> -->
    </div>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  props: {
  },
  data: function () {
    return {
      counter: 0,
      params: {
        counter: 0
      },
    }
  },
  methods: {
    handleClick() {
      this.counter++;
      this.params.counter++;
    },
  },
}
</script>

